import axios from "axios";
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
function People() {
  const [People, setPeople] = useState([]);
  async function getPeople(pageNumber) {
    let { data } = await axios.get(
      `https://api.themoviedb.org/3/person/popular?api_key=4ed164f762a812ca0ce1213e49c1c2b6&include_adult=false&include_video=false&language=en-US&page=${pageNumber}&sort_by=popularity.desc`
    );
    setPeople(data.results);
  }
  useEffect(() => {
    getPeople(1);
  }, []);
  let nums = new Array(13).fill().map((ele, index)=> index + 1)
  return (
    <>
      
          <div className="row justify-content-center">
            {People.map((people, i) => (
              <div key={i} className="col-md-2">
                <Link to={`/peopledetails/${people.id}`}>
                  <div className="movie">
                    {people.profile_path? 
                   <img
                      className="w-100"
                      src={
                        `https://image.tmdb.org/t/p/w500` + people.profile_path
                      }
                      alt={people.name}
                    />  :<img src="./avatar.png" className="w-100" alt="" />
                  }
                    <h2 className="h6 text-center mt-2 mb-2">{people.name}</h2>
                  </div>
                </Link>
              </div>
            ))}
          </div>
          <nav aria-label="..." className="">
  <ul className="pagination pagination-sm justify-content-center align-item-center ">
   {nums.map((num, i) =><li onClick={()=>{getPeople(num)}} key={i} className="page-item"><span className="page-link bg-transparent border-1 active text-light nav-link pointer">{num}</span></li>)}
  </ul>
</nav>
    </>
  );
}

export default People;
