import React from 'react';
import { Link } from "react-router-dom";

function Navbar(props) {
  return (
    <nav className="navbar navbar-expand-lg bg-transparent navbar-dark">
      <div className="container">
        <Link className="navbar-brand" to="/">
          Novo Movies
        </Link>
        <span
          className="navbar-toggler border-0"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </span>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            {props.userInfo? 
            <><li className="nav-item">
                <Link className="nav-link" to="home">
                  Home
                </Link>
              </li><li className="nav-item">
                  <Link className="nav-link" to="movies">
                    Movies
                  </Link>
                </li><li className="nav-item">
                  <Link className="nav-link" to="people">
                    People
                  </Link>
                </li><li className="nav-item">
                  <Link className="nav-link" to="tv">
                    TV
                  </Link>
                </li></>:''}
            
          </ul>

        </div>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav ms-auto mb-2 mb-lg-0 d-flex">
            <li className="nav-item d-flex align-items-center order-lg-first order-last">
            
            <i className="fab mx-2 fa-instagram"></i>
            <i className="fab mx-2 fa-whatsapp"></i>
            <i className="fab mx-2 fa-facebook"></i>
            <i className="fab mx-2 fa-spotify"></i>
            <i className="fab mx-2 fa-youtube"></i>
            
            </li>
            {props.userInfo? <li className="nav-item order-lg-last order-first ">
              <span  className="nav-link pointer" onClick={props.logout}>Logout</span>
            </li>: <><li className="nav-item order-lg-last order-first">
              <Link className="nav-link" to="Login">
                Login
              </Link>
            </li>
            <li className="nav-item order-lg-last order-first">
              <Link className="nav-link" to="register">
                Register
              </Link>
            </li></> }
          
           
          </ul>
        </div>
      </div>
    </nav>
  );
}

export default Navbar