import Axios from 'axios'
import Joi from 'joi'
import React from 'react'
import { useState } from 'react'
import { useNavigate } from 'react-router-dom'
function Login(props) {
	let navigate=useNavigate()
	const [errorList,setErrorList]=useState([])
	const [error ,setError] = useState('')
	const [isLoading,setIsLoading]=useState(false)
	const [user ,setUser] = useState({
		email:'',
		password:''
	})
	function getUserData (e){
		let myUser ={...user}
		myUser[e.target.name] =e.target.value;
		setUser(myUser)
	}
	async function LoginSubmit(e){
		e.preventDefault();
		let validationResult = validateLoginForm()
		if (validationResult.error){
			setErrorList(validationResult.error.details)
			console.log(validationResult.error.details);
			setIsLoading(true)
		}
		else{
		let {data} =await Axios.post('https://movies-api.routemisr.com/signin' ,user)
		if (data.message ==='success'){
			localStorage.setItem('userToken',data.token)
			props.saveUserData()
			//go to login page
			
			navigate('/home')
		}else{
			setError(data.message)
			setIsLoading(false)
		}
		}
	}
	function validateLoginForm() {
		let schema =Joi.object({
			email: Joi.string().email({ minDomainSegments: 2, tlds: { allow: ['com', 'net'] } }),
			password:Joi.string().pattern(new RegExp('^[a-zA-Z0-9]{3,30}$'))
		})
		return schema.validate(user,{abortEarly:false})
	}
  return (
	<>
	<div className="w-75 mx-auto">
	<h2> Login now</h2>
	{errorList.map((errorMessage ,i)=> i===1 ?<div  key={i} className="py-2 alert alert-danger">Password invalid</div>:<div key={i} className="py-2 alert alert-danger">{errorMessage.message}</div>)}
	{error?<div className="alert alert-danger">{error}</div>:''}
	<form onSubmit={LoginSubmit}>
		<label htmlFor="email"> Email :</label>
		<input type="email"  onChange={getUserData} className='form-control bg-transpernt mb-2' id='email' name='email' />

		<label htmlFor="password">  Password :</label>
		<input type="password" onChange={getUserData} className='form-control bg-transpernt mb-2' id='password' name='password' />	

		<button className="btn btn-outline-primary"> {isLoading ===true ?<i className='fas fa-spinner fa-spin'></i>:'Login'}</button>
	</form>
	</div>
	</>
  )
}

export default Login