/* eslint-disable react-hooks/exhaustive-deps */
import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'


function MovieDetails() {
  let [movieDetails , setMovieDetails] =useState([])
  let params = useParams()
async function getMovieDetails(id){
let {data} =await axios.get(`https://api.themoviedb.org/3/movie/${id}?api_key=4ed164f762a812ca0ce1213e49c1c2b6`)
setMovieDetails(data)
}
useEffect(()=>{
  getMovieDetails(params.id)
},[])
  return (
<>
{movieDetails?<div className="row" >
<div className="col-md-3">
  <img className='w-100' src={`https://image.tmdb.org/t/p/w500` + movieDetails.poster_path} alt={movieDetails.title}title={movieDetails.title} />
</div>
<div className="col-md-9">
  <h3>{movieDetails.title}</h3>
  <p className='text-muted'>{movieDetails.overview}</p>
  <ul>
    <li className='mt-3'>release date : {movieDetails.release_date}</li>
    <li className='mt-3'>vote : {movieDetails.vote_average}</li>
    <li className='mt-3'>vote count : {movieDetails.vote_count}</li>
    <li className='mt-3'>status : {movieDetails.status}</li>
    <li className='mt-3'>revenue : {movieDetails.revenue}</li>
    <li className='mt-3'>tagline : {movieDetails.tagline}</li>
    <li className='mt-3'>runtime : {movieDetails.runtime}</li>
    <li className='mt-3'>budget : {movieDetails.budget}</li>


  </ul>

</div>



</div>:<div className='v-100 d-flex align-item-center justify-content-center fs'> 
<i className="text-light fas fa-spinner fa-spin fa-3x"></i>

</div>}
</>
  )
}

export default MovieDetails