import axios from "axios";
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
function Movies() {
  const [movies, setMovies] = useState([]);
  async function getMovies(pageNumber) {
    let { data } = await axios.get(
      `https://api.themoviedb.org/3/discover/movie?api_key=4ed164f762a812ca0ce1213e49c1c2b6&include_adult=false&include_video=false&language=en-US&page=${pageNumber}&sort_by=popularity.desc`
    );
    setMovies(data.results);
  }
  useEffect(() => {
    getMovies(1);
  }, []);
  let nums = new Array(13).fill().map((ele, index)=> index + 1)

  return (
    <>
      
          <div className="row justify-content-center">
            {movies.map((movie, i) => (
              <div key={i} className="col-md-2">
                <Link to={`/moviedetails/${movie.id}`}>
                  <div className="movie">
                    <img
                      className="w-100"
                      src={
                        `https://image.tmdb.org/t/p/w500` + movie.poster_path
                      }
                      alt={movie.title}
                    />
                    <h2 className="h6 text-center mt-2 mb-2">{movie.title}</h2>
                  </div>
                </Link>
              </div>
            ))}
          </div>
          <nav aria-label="..." className="">
  <ul className="pagination pagination-sm justify-content-center align-item-center ">
   {nums.map((num, i) =><li onClick={()=>{getMovies(num)}} key={i} className="page-item"><span className="page-link bg-transparent border-1 active text-light nav-link pointer">{num}</span></li>)}
  </ul>
</nav>
    </>
  );
}

export default Movies;
