// import Axios from 'axios'
import axios from 'axios'
import Joi from 'joi'
import React from 'react'
import { useState } from 'react'
import { useNavigate } from 'react-router-dom'
function Register() {
	let navigate=useNavigate()
	const [errorList,setErrorList]=useState([])
	const [error ,setError] = useState('')
	const [isLoading,setIsLoading]=useState(false)
	const [user ,setUser] = useState({
		first_name:'',
		last_name:'',
		email:'',
		password:'',
		age:''
	});
	function getUserData (e){
		let myUser ={...user}
		myUser[e.target.name] = e.target.value;
		setUser(myUser);
	}
async function addUser(event){
		event.preventDefault();
		let validationResult = validateRegisterForm()
		if (validationResult.error){
			setErrorList(validationResult.error.details)
			console.log(validationResult.error.details);
			setIsLoading(false)
		}
		else{
 		let {data} = await axios.post("https://movies-api.routemisr.com/signup", user );
		if (data.message ==='success'){
			navigate('/login')
		
		setIsLoading(false)
		}
		else{
			setError(data.errors)
			setIsLoading(false)

		}
		}
	}

	function validateRegisterForm(){
		let schema =Joi.object({
			first_name:Joi.string().alphanum().min(3).max(12).required(),
			last_name:Joi.string().alphanum().min(3).max(12).required(),
			age:Joi.number().min(16).max(80).required(),
			email: Joi.string().email({ minDomainSegments: 2, tlds: { allow: ['com', 'net'] } }),
			password:Joi.string().pattern(new RegExp('^[a-zA-Z0-9]{3,30}$'))

		});
		return schema.validate(user,{abortEarly:false})
		  
	}
  return (
	<>
	<div className="w-75 mx-auto">
	<h2> Register now</h2>
	{errorList.map((errorMessage ,i)=>i===4?<div className="py-2 alert alert-danger">Password invalid</div>:<div key={i} className="py-2 alert alert-danger">{errorMessage.message}</div>)}

	{error?<div className="alert alert-danger">{error.message}</div>:''}
	<form onSubmit={addUser}>

		<label htmlFor="first_name"> First Name :</label>
		<input type="text" onChange={getUserData} className='form-control bg-transpernt mb-2' id='first_name' name='first_name' />

		<label htmlFor="Last_name"> Last Name :</label>
		<input type="text" onChange={getUserData} className='form-control bg-transpernt mb-2' id='Last_name' name='last_name' />

		<label htmlFor="age"> Age :</label>
		<input type="text" onChange={getUserData} className='form-control bg-transpernt mb-2' id='age' name='age' />
		
		<label htmlFor="email"> Email :</label>
		<input type="email"  onChange={getUserData} className='form-control bg-transpernt mb-2' id='email' name='email' />

		<label htmlFor="password">  Password :</label>
		<input type="password" onChange={getUserData} className='form-control bg-transpernt mb-2' id='password' name='password' />	

		<button className="btn btn-outline-primary"> {isLoading?<i className='fas fa-spinner fa-spin'></i>:'Rigister'}</button> 
	</form>
	</div>
	</>
  )
}

export default Register