/* eslint-disable react-hooks/exhaustive-deps */
import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'


function PeopleDetails() {
  let [peopleDetails , setPeopleDetails] =useState([])
  let params = useParams()
async function getPeopleDetails(id){
let {data} =await axios.get(`https://api.themoviedb.org/3/person/${id}?api_key=4ed164f762a812ca0ce1213e49c1c2b6`)
setPeopleDetails(data)
}
useEffect(()=>{
  getPeopleDetails(params.id)
},[])
  return (
<>
{peopleDetails?<div className="row" >
<div className="col-md-3">
  <img className='w-100' src={`https://image.tmdb.org/t/p/w500` + peopleDetails.profile_path} alt={peopleDetails.title}title={peopleDetails.title} />
</div>
<div className="col-md-9">
  <h3>{peopleDetails.name}</h3>
  <p className='text-muted'>{peopleDetails.biography}</p>
  <ul>
    <li className='mt-3'>gender : {peopleDetails.gender===2 ?'male':'female'}</li>
    <li className='mt-3'>birthday : {peopleDetails.birthday}</li>
    <li className='mt-3'>place of birth : {peopleDetails.place_of_birth}</li>
    <li className='mt-3'>known for department : {peopleDetails.known_for_department}</li>
  </ul>

</div>



</div>:<div className='v-100 d-flex align-item-center justify-content-center fs'> 
<i className="text-light fas fa-spinner fa-spin fa-3x"></i>

</div>}
</>
  )
}

export default PeopleDetails