/* eslint-disable react-hooks/exhaustive-deps */
import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'


function TvDetails() {
  let [TvDetails , setTvDetails] =useState([])
  let params = useParams()
async function getTvDetails(id){
let {data} =await axios.get(`https://api.themoviedb.org/3/tv/${id}?api_key=4ed164f762a812ca0ce1213e49c1c2b6`)
setTvDetails(data)
}
useEffect(()=>{
  getTvDetails(params.id)
} , [ ])
  return (
<>
{TvDetails?<div className="row" >
<div className="col-md-3">
  <img className='w-100' src={`https://image.tmdb.org/t/p/w500` + TvDetails.poster_path} alt={TvDetails.title}title={TvDetails.title} />
</div>
<div className="col-md-9">
  <h3>{TvDetails.name}</h3>
  <p className='text-muted'>{TvDetails.overview}</p>
  <ul>
    <li className='mt-3'>release date : {TvDetails.release_date}</li>
    <li className='mt-3'>vote : {TvDetails.vote_average}</li>
    <li className='mt-3'>vote count : {TvDetails.vote_count}</li>
    <li className='mt-3'>status : {TvDetails.status}</li>
    <li className='mt-3'>first air date : {TvDetails.first_air_date}</li>
    <li className='mt-3'>last air date : {TvDetails.last_air_date}</li>
    <li className='mt-3'>type : {TvDetails.type}</li>
    <li className='mt-3'>popularity : {TvDetails.popularity}</li>


  </ul>

</div>



</div>:<div className='v-100 d-flex align-item-center justify-content-center fs'> 
<i className="text-light fas fa-spinner fa-spin fa-3x"></i>

</div>}
</>
  )
}

export default TvDetails