import { Navigate, Route, Routes, useNavigate } from "react-router-dom";
// import Footer from "./components/Footer/Footer";
import Home from "./components/Home/Home";
import Movies from "./components/Movies/Movies";
import Navbar from "./components/Navbar/Navbar";
import Tv from "./components/TV/Tv";
import People from './components/People/People';
import NotFoundPage from './components/NotFoundPage';
import Register from './components/Register/Register';
import Login from "./components/Login/Login";
import { useEffect, useState } from "react";
import jwtDecode from "jwt-decode";
import MovieDetails from './components/Movies/MovieDetails';
import TvDetails from "./components/TV/TvDetails";
import PeopleDetails from "./components/People/PeopleDetails";


function App() {
  let [userInfo ,setUserInfo] = useState(null);
  let navigate =useNavigate()
  function saveUserData(){
    let encodedToken = localStorage.getItem('userToken')
  
  let decodedToken = jwtDecode(encodedToken)
  setUserInfo(decodedToken)
 }

 function ProtectedRoutte(props){
  if (localStorage.getItem('userToken')===null){
//navigate to login
return <Navigate to='/login'/>
  }else{
//navigate to component
return props.children
  }
 }
 useEffect(()=>{
 if(localStorage.getItem('userToken')){
  saveUserData()
 }
 },[])
 function logout(){

  // localStorage.clear()
 setUserInfo(null)
 localStorage.removeItem('userToken')
 navigate('/login')

 }
    return (
    <>
      <Navbar userInfo={userInfo } logout={logout}/>
      <div className="container-fluid">
      <Routes>
        <Route path="/" element={<ProtectedRoutte><Home/></ProtectedRoutte>} />
        <Route path="home" element={<ProtectedRoutte><Home /></ProtectedRoutte>} />
        <Route path="movies" element={<ProtectedRoutte><Movies /></ProtectedRoutte>}/>
        <Route path="moviedetails" element={<ProtectedRoutte><MovieDetails /></ProtectedRoutte>}>
            <Route path=":id" element={<ProtectedRoutte><MovieDetails /></ProtectedRoutte>}/>
        </Route>
        <Route path="tv" element={<ProtectedRoutte><Tv/></ProtectedRoutte>} />
        <Route path="tvdetails" element={<ProtectedRoutte><TvDetails /></ProtectedRoutte>}>
            <Route path=":id" element={<ProtectedRoutte><TvDetails /></ProtectedRoutte>}/>
        </Route>
        <Route path="people" element={<ProtectedRoutte><People /></ProtectedRoutte>} />
        <Route path="peopledetails" element={<ProtectedRoutte><PeopleDetails /></ProtectedRoutte>}>
            <Route path=":id" element={<ProtectedRoutte><PeopleDetails /></ProtectedRoutte>}/>
        </Route>
        <Route path="register" element={<Register />} />
        <Route path="login" element={<Login saveUserData={saveUserData}/>} />
        <Route path="*" element={<NotFoundPage />}/>
      </Routes>
      </div>

      {/* <Footer /> */}
    </>
  );
}

export default App;
