import axios from "axios";
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";

function Home() {
  const [trendingMovies, setTrendingMovies] = useState([]);
  const [trendingTv, setTrendingTv] = useState([]);
  const [trendingPerson, setTrendingPerson] = useState([]);

  async function getData(mediaType, callback) {
    let { data } = await axios.get(
      `https://api.themoviedb.org/3/trending/${mediaType}/week?api_key=4ed164f762a812ca0ce1213e49c1c2b6`
    );
    callback(data.results.slice(0, 10));
  }
  useEffect(() => {
    getData("tv", setTrendingTv);
    getData("movie", setTrendingMovies);    
    getData("person", setTrendingPerson);
  }, []);

  return (
    <>
      <div className="row mt-5">
        <div className="col-md-4 d-flex align-items-center justify-content-center">
          <div className="">
            <div className="brder mb-3 w-25"></div>
            <h3>
              Trending <br /> Movies <br /> To watch Right Now
            </h3>
            <p className="text-muted">Top Trending Movies by week</p>
            <div className="brder mt-3"></div>
          </div>
        </div>
        {trendingMovies.map((movie, i) => (
          <div key={i} className="col-md-2">
            <Link to={`/moviedetails/${movie.id}`}>
              <div className="movie">
                <img
                  className="w-100"
                  src={`https://image.tmdb.org/t/p/w500` + movie.poster_path}
                  alt={movie.title}
                />
                <h2 className="h6 text-center mt-2 mb-2">{movie.title}</h2>
              </div>
            </Link>
          </div>
        ))}
      </div>
      <div className="row mt-5">
        <div className="col-md-4 d-flex align-items-center justify-content-center">
          <div className="">
            <div className="brder mb-3 w-25"></div>
            <h3>
              Trending <br /> TV <br /> To watch Right Now
            </h3>
            <p className="text-muted">Top Trending TV by week</p>
            <div className="brder mt-3"></div>
          </div>
        </div>
        {trendingTv.map((tv, i) => (
          <div key={i} className="col-md-2">
            <Link to={`/tvdetails/${tv.id}`}>
            <div className="tv">
              <img
                className="w-100"
                src={`https://image.tmdb.org/t/p/w500` + tv.poster_path}
                alt={tv.name}
              />
              <h2 className="h6 text-center mt-2 mb-2">{tv.name}</h2>
            </div>
            </Link>
          </div>
        ))}
      </div>
      <div className="row mt-5">
        <div className="col-md-4 d-flex align-items-center justify-content-center">
          <div className="person">
            <div className="brder mb-3 w-25"></div>
            <h3>
              Trending <br /> Person <br /> To watch Right Now
            </h3>
            <p className="text-muted">Top Trending Person by week</p>
            <div className="brder mt-3"></div>
          </div>
        </div>
        {trendingPerson.map((pereson, i) => (
          <div key={i} className="col-md-2">
          <Link to={`/peopledetails
          /${pereson.id}`}>

            <div className="person">
              {pereson.profile_path === null ? (
                <img src="./avatar.png" className="w-100" alt="" />
              ) : (
                <img
                  className="w-100"
                  src={`https://image.tmdb.org/t/p/w500` + pereson.profile_path}
                  alt={pereson.name}
                />
              )}
              <h2 className="h6 text-center mt-2 mb-2">{pereson.name}</h2>
            </div></Link>
          </div>
        ))}
      </div>
    </>
  );
}

export default Home;
